@media screen and (max-width: 420px) {
  .cart {
    right: 4.6rem !important;
    top: 10.5rem !important;
  }
}

.cart {
  position: absolute;
  width: 330px;
  right: 6rem;
  top: 5.5rem;
  z-index: 999;
  background-color: #fff;
  padding: 20px;
  -webkit-box-shadow: 1px 2px 12px -3px rgba(204, 204, 204, 0.59);
  -moz-box-shadow: 1px 2px 12px -3px rgba(204, 204, 204, 0.59);
  box-shadow: 1px 2px 12px -3px rgba(204, 204, 204, 0.59);

  .cartHeader {
    display: flex;
    margin-bottom: 30px;

    h1 {
      flex: 16;
      color: rgb(29, 29, 29);
      font-weight: 700;
      font-size: 12px;
      text-transform: uppercase;
    }

    .cartCloseBtn {
      flex: 1;
      background-color: transparent;
    }
  }

  .item {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 30px;

    img {
      width: 80px;
      height: 100px;
      object-fit: cover;
    }

    .details {
      h1 {
        font-size: 12px;
        font-weight: 500;
      }

      p {
        color: rgb(110, 109, 109);
        margin-bottom: 10px;
        font-size: 12px;
      }

      .price {
        color: black;
      }
    }

    .delete {
      color: black;
      font-size: 16px;
      cursor: pointer;
    }
  }

  .total {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
    font-size: 12px;
    margin-bottom: 20px;
  }

  .checkoutBtn {
    width: 100%;
  }

  .cartCloseBtn {
    display: inline-block;
    width: 12px;
    padding: 0px;
    background-color: white;
    margin-bottom: 0px;
  }

  .cartCloseBtn:hover {
    background-color: white;
    color: black;
  }

  button {
    width: 250px;
    padding: 10px;
    background-color: #f4f3f3;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    cursor: pointer;
    border: none;
    font-size: 12px;
    font-weight: 500;
    margin-bottom: 15px;
  }

  button:hover {
    background-color: black;
    color: #f4f3f3;
  }

  .reset {
    color: rgb(31, 31, 31);
    font-size: 12px;
    cursor: pointer;
    margin-top: 5px;
    margin-bottom: 5px;
  }
}
